import React, { Component } from 'react';
import { Redirect } from 'react-router';
import MaskedInput from 'react-text-mask';
import Hero from './Hero';
import Button from './Button';
import appStyles from './App.scss';
import s from './Estimate.scss';

const contactMethods = [
  {
    label: 'Phone',
    value: 'phone'
  },
  {
    label: 'Email',
    value: 'email'
  }
];

const referrers = [
  {
    label: 'Angie\'s List',
    value: 'angiesList'
  },
  {
    label: 'Door Hanger',
    value: 'doorHanger'
  },
  {
    label: 'Google',
    value: 'google'
  },
  {
    label: 'IWCA.org',
    value: 'iwca'
  },
  {
    label: 'Yahoo',
    value: 'yahoo'
  },
  {
    label: 'Other Web Link',
    value: 'webLink'
  },
  {
    label: 'Vehicle Graphics',
    value: 'vehicleGraphics'
  },
  {
    label: 'Personal Referral (Please Specify)',
    value: 'personalReferral'
  },
  {
    label: 'Other (Please Specify)',
    value: 'other'
  }
];

const titles = [
  {
    label: 'Mr.',
    value: 'mr'
  },
  {
    label: 'Mrs.',
    value: 'mrs'
  },
  {
    label: 'Ms.',
    value: 'ms'
  },
  {
    label: 'Miss',
    value: 'miss'
  },
  {
    label: 'Dr.',
    value: 'dr'
  }
];

const services = [
  {
    label: 'Window cleaning inside & outside',
    value: 'windowsInsideOutside'
  },
  {
    label: 'Window cleaning outside only',
    value: 'windowsOutside'
  },
  {
    label: 'Clean skylights',
    value: 'skylights'
  },
  {
    label: 'Clean divided lights (French panes)',
    value: 'frenchPanes'
  },
  {
    label: 'Clean transoms (over interior doors)',
    value: 'transoms'
  },
  {
    label: 'Clean French doors (interior)',
    value: 'interiorFrenchDoors'
  },
  {
    label: 'Clean chandelier',
    value: 'chandelier'
  },
  {
    label: 'Clean storm windows',
    value: 'stormWindows'
  },
  {
    label: 'Clean debris from gutters and downspouts',
    value: 'gutters'
  },
  {
    label: 'Low pressure house wash',
    value: 'houseWash'
  },
  {
    label: 'Pressure wash driveway',
    value: 'driveway'
  },
  {
    label: 'Pressure wash walkway',
    value: 'walkway'
  },
  {
    label: 'Pressure wash patio',
    value: 'patio'
  }
];

const Label = ({children, ...props}) => (
  <label className={s.label} {...props}>
    {children}
  </label>
);

const Input = props => (
  <input className={s.text} {...props} />
);

const Checkbox = props => (
  <input className={s.checkbox} type="checkbox" {...props} />
);

const Select = props => {
    const {options, ...rest} = props;
    return (
      <select className={s.select} {...rest}>
        <option selected disabled>Select one...</option>
        {
          options.map(({value, label}) => (
            <option key={value} value={value}>
              {label}
            </option>
          ))
        }
      </select>
    );
};

const CheckboxGroup = ({name, items, handleChange}) => (
  <div className={s.checkboxGroup}>
    {
      items.map(({value, label}) => (
        <div key={value}>
          <Checkbox name={name}
                    id={value}
                    value={value}
                    onChange={handleChange} />
          <Label htmlFor={value}>{label}</Label>
        </div>
      ))
    }
  </div>
);

class Estimate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fireRedirect: false,
      formData: {
        subject: 'New Estimate Request',
        recipient: 'estimate1@drsqueegee.ca',
        sort: 'order:title,firstName,lastName,address,email,phone,contactMethod,requestedServices,referrer,referrerDetails,comments',
        env_report: 'REMOTE_HOST,REMOTE_ADDR,HTTP_USER_AGENT',
        requestedServices: new Set()
      }
    };
  }

  handleChange(propertyName, e) {
    console.log(propertyName, e.target.value);
    let formData = {...this.state.formData};
    formData[propertyName] = e.target.value;

    this.setState({ formData });
    console.log(this.state);
  }

  toggleService(e) {
    const service = e.target.value;
    this.state.formData.requestedServices.has(service) ? this.state.formData.requestedServices.delete(service) : this.state.formData.requestedServices.add(service);
    console.log(this.state);
  }

  handleSubmit(e) {
    e.preventDefault();
    console.log(this.state);

    const formData = {...this.state.formData};
    formData.requestedServices = [...formData.requestedServices];
    formData.realname = `${formData.firstName} ${formData.lastName}`;

    fetch('http://www.drsqueegee.ca/cgi-bin/formmail/formmail.cgi', {
      method: 'POST',
      mode: 'no-cors',
      body: Object.keys(formData).map(i => `${i}=${formData[i]}`).join('&'),
      headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then(res => {
      this.setState({fireRedirect: true});
        //return res;
    }).catch(err => err);
  }

  otherRefferer() {
    const referrer = this.state.formData.referrer;
    return referrer === 'personalReferral' || referrer === 'other';
  }

  render() {
    const { from } = this.props.location.state || '/';
    const { fireRedirect } = this.state;

    return (
      <div className={s.root}>

        <Hero className={s.hero} heading="Get a free estimate.">
          <p>Request for service or a free estimate online or on the phone (780) 669-9748.</p>
        </Hero>

        <form className={s.form} onSubmit={this.handleSubmit.bind(this)}>

          <Label htmlFor="title">Title</Label>
          <Select id="title"
                  options={titles}
                  onChange={this.handleChange.bind(this, 'title')}
                  required />

          <Label htmlFor="firstName">First name</Label>
          <Input type="text"
                 id="firstName"
                onChange={this.handleChange.bind(this, 'firstName')}
                required />

          <Label htmlFor="lastName">Last name</Label>
          <Input type="text"
                 id="lastName"
                 onChange={this.handleChange.bind(this, 'lastName')}
                 required />

          <Label htmlFor="address">Address</Label>
          <Input type="text"
                 id="address"
                 onChange={this.handleChange.bind(this, 'address')}
                 required />

          <Label htmlFor="email">Email</Label>
          <Input type="email"
                 id="email"
                 placeholder="example@domain.com"
                 onChange={this.handleChange.bind(this, 'email')}
                 required />

          <Label htmlFor="phone">Phone</Label>
          <MaskedInput className={s.text}
                       mask={['(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
                       type="tel"
                       id="phone"
                       placeholder="(___) ___-____"
                       pattern="[(][0-9]{3}[)] [0-9]{3}-[0-9]{4}"
                       onChange={this.handleChange.bind(this, 'phone')}
                       required />

          <Label htmlFor="contactMethod">Preferred contact method (optional)</Label>
          <Select
            id="contactMethod"
            options={contactMethods}
            onChange={this.handleChange.bind(this, 'contactMethod')} />

          <div className={s.subheading}>What services are you interested in? <small className={s.small}>(check all that apply)</small></div>

          <CheckboxGroup name="requestedServices"
                         items={services}
                         handleChange={this.toggleService.bind(this)} />

          <Label htmlFor="referrer">How did you hear about us? (optional)</Label>
          <Select id="referrer"
                  options={referrers}
                  onChange={this.handleChange.bind(this, 'referrer')} />

          <textarea id="referrerDetails"
                    className={`${s.textarea} ${!this.otherRefferer() ? s.hidden : ''}`}
                    rows="4"
                    placeholder="Tell us about the referral."
                    onChange={this.handleChange.bind(this, 'referrerDetails')} />

          <Label htmlFor="comments">Any additional comments? (optional)</Label>
          <textarea id="comments"
                    className={`${s.textarea} ${s.comments}`}
                    rows="4"
                    onChange={this.handleChange.bind(this, 'referrerDetails')} />

          <button type="submit">Submit</button>
        </form>
        {fireRedirect && (
          <Redirect to={from || 'thanks'}/>
        )}
      </div>
    );
  }
}

export default Estimate;



