import React, { Component } from 'react';
import s from './Hero.scss';

class Hero extends Component {
  render() {
    return (
      <div className={`${s.root} ${this.props.className}`}>
        <h1 className={s.heading}>{this.props.heading}</h1>
        {this.props.children}
      </div>
    );
  }
}

export default Hero;



