/* eslint import/no-webpack-loader-syntax: off */

import React, { Component } from 'react';
import Slider from 'react-slick';
import "!style-loader!css-loader!slick-carousel/slick/slick.css";
import "!style-loader!css-loader!slick-carousel/slick/slick-theme.css";
import "!style-loader!css-loader!./slick-theme.scss";
import s from './Carousel.scss';

class Carousel extends Component {
  render () {
    var settings = {
      infinite: true,
      dots: true,
      arrows: false,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      variableWidth: true,
      autoplay: true,
      autoplaySpeed: 5000,
      lazyload: true
    };
    return (
      <Slider {...settings}>
        {this.props.images.map((image, i) => {
          console.log(image);
          return(
            <div className={s.slide}>
              <img className={s.image} src={image} />
            </div>
          )
        })}
      </Slider>
    );
  }
}

export default Carousel;



