import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import logo from './logo-color.svg';
import s from './Header.scss';

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fullScreenOpen: false
    }
  }

  setFullScreenOpen(open) {
    this.setState({fullScreenOpen: open});
  }

  render() {
    return (
      <div className={`${s.root} ${this.state.fullScreenOpen ? s.open : ''}`}>

        <div className={s.hamburger}
             onClick={() => this.setFullScreenOpen(!this.state.fullScreenOpen)}>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>

        <Link to="/"
              className={s.logo}
              onClick={() => this.setFullScreenOpen(false)}>
          <img src={logo}
               className={s.logoImage}
               alt="Dr. Squeegee" />
        </Link>

        <div className={s.fullscreen}>
          <ul>
            <li>
              <Link to="windows" onClick={() => this.setFullScreenOpen(false)}>Window Cleaning</Link>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default Header;



