import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Hero from './Hero';
import Button from './Button';
import Carousel from './Carousel';
import Card from './Card';
import Quote from './Quote';
import s from './Home.scss';
import sCard from './Card.scss';
import buttonStyles from './Button.scss';

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
  return images;
}

const images = importAll(require.context('./carouselImages', false, /\.jpe?g$/));

class Home extends Component {
  render() {
    return (
      <div>
        <Hero
          className={s.hero}
          heading="The Edmonton area's best choice for window, chandelier, &amp; gutter cleaning.">
          <Link to="estimate" className={buttonStyles.root}>
            Get your free estimate
          </Link>
        </Hero>
        <p className={s.intro}>We are a professional window cleaning company, servicing Edmonton, Sherwood Park, St. Albert, and Fort Saskatchewan, specializing in residential window, chandelier, gutter and pressure cleaning.
        </p>
        <Carousel images={Object.values(images)}/>
        <div className={s.content}>
          <p>If you're like most of our customers, you don't do windows! That's because window cleaning is time consuming and often difficult or dangerous.</p>
          <p>If climbing ladders and hanging out of a window isn't "your thing", then just sit back and be glad you called the professionals! We have the equipment and the expertise to handle the job.</p>
          <p>We take pride in showing up on time, and delivering a little extra.</p>
        </div>
        <div className={s.services}>
          <Card
            heading="Window Cleaning"
            text="Vaulted ceilings? Hard to reach window? No problem! We can reach it!"
            image={require('./edmonton-window-cleaning-04.jpg')}
            url="windows"
          />
          <Card
            heading="Gutter Cleaning"
            text="Protect your best investment by keeping your eavestrough / gutter clean of debris."
            image={require('./edmonton-gutter-cleaning-01.jpg')}
            url="gutters"
          />
        </div>
        <div>
          <Quote
            text={['I would and will again recommend you to anyone for ', <strong>the best looking windows I have ever seen.</strong>]}
            source="Duane C, Sherwood Park, AB"
          />
        </div>
      </div>
    );
  }
}

export default Home;



